.personal-information {
    padding: 1.5rem 0 1rem 0;
}
.personal-name {
    margin:0 auto;
    display: block;
    padding: 0.5rem 0 1rem 0;
}

.personal-name span {
    display: block;
    font-size:1rem;
    font-weight: 700;
    color:var(--white);
    text-align:center;
}

.personal-button{
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.personal-button span {
    display: inline-block;
    padding: 0.4rem 1rem;
    background: var(--yellow);
    border-radius: 1.8rem;
    cursor: pointer;
    margin: 0 1.5rem 0 0;
    display:inline-block;
}

.ant-upload-select img{
    display: block;
    width:100px;
    height:100px;
    border-radius: 50%;
}

.ant-tabs-tab-btn{
    color: var(--white);
}

.anticon-ellipsis svg{
    color: var(--white);
}


.db-table-main{
    padding:1rem 0;
}

.db-table-header{
    background:var(--light-gray);
    color:var(--white);
    padding:0.5rem 1.5rem;
    border-radius: 0.4rem 0.4rem 0 0;
    display: flex;
    justify-content:space-between ;
    align-items: center;
    align-content: center;
    font-weight:700;
    font-size:0.8rem;
}

.db-table-info{
    background:var(--light-gray);
    color:var(--white);
    padding:0.6rem 1.5rem;
    border-radius: 0;
    display: flex;
    justify-content:space-between ;
    align-items: center;
    align-content: center;
    border-top:1px solid var(--line);
    .tocopy{
        font-style:normal;
        padding:0 0 0 1rem;
        cursor: pointer;
    }
    .db-table-item.db-table-item-title{
        text-decoration: none;
    }
    .db-table-item{
        width:50%;
        text-align:center;
        text-decoration: underline;
        cursor:pointer;
    }
    .db-table-items{
        width:50%;
        text-align:center;
    }
    .db-table-price{
        width:25%;
        text-align:center;
    }
    .db-table-prices{
        width:10%;
        text-align:center;
    }
    .db-table-people{
        width:15%;
        text-align:center;
    }
    .db-table-time{
        width:25%;
        text-align:center;
    }
}

.db-table-main .db-table-info:first-child{
    border-radius: 0.4rem 0.4rem 0 0 ;
    font-weight:700;
}

.db-table-main .db-table-info:last-of-type{
    border-radius: 0 0 0.4rem 0.4rem; 
}


.table-points-info{
    background:var(--light-gray);
    color:var(--white);
    padding:0.6rem 1.5rem;
    border-radius: 0;
    display: flex;
    justify-content:space-between ;
    align-items: center;
    align-content: center;
    border-top:1px solid var(--line);
}

.table-points-oneinfo{
    background:var(--light-gray);
    color:var(--white);
    padding:0.6rem 1.5rem;
    border-radius: 0;
    display: flex;
    justify-content:space-between ;
    align-items: center;
    align-content: center;
    border-radius: 8px;
}

.table-points-info:first-child{
    border-radius: 0.4rem 0.4rem 0 0 ;
}

.table-points-info:last-of-type{
    border-radius: 0 0 0.4rem 0.4rem; 
}


.list-item-info{
    border-radius: 0.4rem ;
    background:var(--light-gray);
    margin:0 0 1rem 0;
    color:var(--white);
    .item-title{
        padding:0.6rem 1.5rem 0 1.5rem;
        font-size:0.8rem;
    }
    .item-foot{
        border-top:1px solid var(--line);
        display: flex;
        justify-content:flex-end;
        align-items: center;
        align-content: center;
        padding:0.6rem 1.5rem;
        font-size:0.6rem;
        margin-top:0.6rem;
    }
}

.db-buy-info{
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    .db-buy-people{
        width: 50%;
        text-align:center;
        height: 30px;
    }
}

@media (max-width: 767px){
    .personal-button span{
        margin:  0 0.8rem 0 0;
    }
}