html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    font-family: "Manrope", sans-serif;
    /* 	font-family: "YouTube Sans", "Roboto", sans-serif; */
    font-size: 0.7rem;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

ol,
ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

p,
span,
h2 {
    margin: 0;
    padding: 0;
}

::-webkit-scrollbar {
    display: none;
}

$imgBaseUrl: "../images";


@font-face {
    font-family: 'iconfont';
    src: url('iconfont.woff2?t=1689055175095') format('woff2'),
    url('iconfont.woff?t=1689055175095') format('woff'),
    url('iconfont.ttf?t=1689055175095') format('truetype');
}

.iconfont {
    font-family: "iconfont" !important;
    font-size:1rem;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


a {
    color: var(--white);
}

a:hover {
    color: var(--tab-color);
}

:where(.css-dev-only-do-not-override-111pooj).ant-form-item .ant-form-item-label >label{
    color: var(--white);
}
:where(.css-dev-only-do-not-override-111pooj).ant-radio-wrapper{
    color: var(--white);
}
.ant-upload-list-item-name,.ant-upload-icon svg,.ant-btn-icon svg{
    color: var(--white);
}


:where(.css-dev-only-do-not-override-14wwjjs).ant-select-dropdown .ant-select-item-option-selected:not(.ant-select-item-option-disabled){
    background-color:#f9e296;
}
:where(.css-dev-only-do-not-override-14wwjjs).ant-select:not(.ant-select-disabled):not(.ant-select-customize-input):not(.ant-pagination-size-changer):hover .ant-select-selector{
    border-color:#f9e296;
}
.ant-select-focused:where(.css-dev-only-do-not-override-14wwjjs).ant-select:not(.ant-select-disabled):not(.ant-select-customize-input):not(.ant-pagination-size-changer) .ant-select-selector{
    border-color:#f9e296;
}

:where(.css-dev-only-do-not-override-14wwjjs).ant-btn-primary{
    color:var(--black);
    background-color: #f9e296;
}

:where(.css-dev-only-do-not-override-14wwjjs).ant-btn-primary:not(:disabled):not(.ant-btn-disabled):hover{
    color:var(--black);
    background-color:#f9e296;
}

:where(.css-dev-only-do-not-override-14wwjjs).ant-btn-default:not(:disabled):not(.ant-btn-disabled):hover{
    color:var(--black);
    border-color: #f9e296;
}

:where(.css-dev-only-do-not-override-14wwjjs).ant-switch.ant-switch-checked{
    background: #f9e296;
}
:where(.css-dev-only-do-not-override-14wwjjs).ant-switch{
    background:#666;
}
:where(.css-dev-only-do-not-override-14wwjjs).ant-switch.ant-switch-checked:hover:not(.ant-switch-disabled){
    background: #f9e296;
}

:where(.css-dev-only-do-not-override-111pooj).ant-pagination .ant-pagination-options {
    display: none;
    margin-inline-start: 16px;
    vertical-align: middle;
}

.ant-layout {
    flex: auto;
    min-height: 0;
    overflow-y: auto;
    height: calc(100vh - 46px);
}

.main-right{
    background:var(--deep-black);
    padding:1rem 1rem 1rem 0;
}

.loading{
    margin: 20px 0;
    margin-bottom: 20px;
    padding: 30px 50px;
    text-align: center;
    border-radius: 4px;
}


@media (max-width: 767px){
    .main-right {
        padding:1rem;
    }
}


