.header {
    height: 2.8rem;
    z-index: 10;
    background: var(--black);
    color: var(--white);
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #32383d;
}

.header-logo-test{
    margin-left:1.5rem;
    font-size:18px;
    font-style:italic;
}


.header-logo {
    margin-left:1.5rem;
    width: 6rem;
    height:2rem;
    background-image: url('../images/logo.png');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

.header-nav{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-content: center;
}

.navbar-item {
    padding: 0 1rem 0 0;
}

.header-reg {
    background: var(--yellow);
    padding: 0.3rem 1.4rem;
    margin-right: 1rem;
    border-radius: 1.6rem;
    cursor: pointer;
    color: var(--black);
    border: 1px solid var(--yellow);
}

.header-login{
    background: var(--black);
    color: var(--white);
}

.header-menu{
    padding:0.4rem 1rem 0 0;
    display:none;
}

.wallet-reg {
    padding: 0 0 1rem 0;
}


.wallet-modal-item {
    display: flex;
    align-items: center;
}

.wallet-modal {
    cursor: pointer;
    background: linear-gradient(1deg, #292A2C, #4D4E51);
    box-shadow: 0px 6px 27px 0px rgba(70, 70, 70, 0.3);
    border: 1px solid #414245;
    border-radius: 0.9rem;
    height: 3rem;
    box-sizing: border-box;
    padding: 0 0.4rem 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0.5rem 0;
}

.wallet-modal-title {
    padding-left: 0.5rem;
    font-weight: 700;
    font-size: 0.8rem;
    color:#fff;
}


.modal-wallet-content{
    transform: translate(-12px, 67px);
    z-index: 2!important;
    width: 300px;
    background-color: var(--black);
    position: fixed;
    right:0;
    background-clip: padding-box;
    top: 0px;
    border-radius: 8px;
    box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 9px 28px 8px rgba(0, 0, 0, 0.05);
    pointer-events: auto;
    padding: 20px 24px;
    display: none;
    animation: moving 1s linear;
}

@keyframes moving {
    0% {
        filter: opacity(0);
    }
    100% {
        filter: opacity(1);
    }
}

.modal-wallet-content.is-active{
    display: block;
}

.oaa-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    .oaa-title-peo{
        display: flex;
        justify-content:flex-start;
        align-items: center;
        align-content: center;
    }
    .peo-img img{
        width:50px;
        height:50px;
        display: block;
        border-radius: 50%;
    }
    .peo-name{
        padding:0 20px 0 10px;
        font-weight:700;
        width:200px;
        word-wrap:break-word;
    }
}
.oaa-select{
    padding:20px 0;
}
.oaa-select-info{
    display: flex;
    justify-content:space-between;
    align-items: center;
    align-content: center;
    padding:10px 0;
    .peo-reg{
        background: var(--yellow);
        padding: 4px 0;
        border-radius: 8px;
        width:118px;
        text-align:center;
        cursor: pointer;
        color: var(--black);
        border: 1px solid var(--yellow);
    }
}



.select-item-refresh{
    cursor:pointer;
}

.ant-btn-default{
    margin-left:1rem;
}

.nav-line{
    margin:5% 2%;
    width:96%;
    height:1px;
    background:#fff;
}

.remarks{
    margin:20px 0 0 0;
}

@media (max-width: 767px){
    .header-logo {
        margin-left:0.7rem;
        width: 2rem;
        height:2rem;
        background-image: url('../images/slogo.png');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
    }
    .header-menu{
        display:block;
    }
    .navbar-item{
        font-size:10px;
    }
    .header-reg {
        padding: 0.3rem 0.5rem;
        font-size:10px;
    }
}