.home-index-content{
    padding:0 1.5rem;
    background:var(--light-black);
    border-radius: 0.8rem;
    height:calc(100vh - 2rem);
    overflow:auto;
}

.list-nav-title{
    color:var(--white);
    font-size: 1rem;
    margin-top:1.2rem;
}

.list-nav {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.list-nav-item {
    display: inline-block;
    border-radius: 1.8rem;
    padding: 0.3rem 1.15rem;
    font-size: 0.8rem;
    margin: 1rem 1rem 0 0;
    font-weight: 400;
    color:#fff;
    cursor: pointer;
    background:#8e8e8e;
}

.list-nav-item-selected {
    background:#f9e296;
    color: #000;
    font-weight: 400;
}

.list-items {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    color: var(--white);
    overflow:hidden;
}

.list-item {
    margin: 1.25rem 4% 0 0;
    width: calc((100% - 8%) / 3);
    min-width: calc((100% - 8%) / 3);
    max-width: calc((100% - 8%) / 3);
    background: var(--deep-black);
    padding: 0.5rem;
    border-radius: 0.8rem;
}

.list-item:last-child {
    margin-right: auto;
}

.list-item:nth-child(3){
    margin: 1.25rem 0 0 0;
}

.list-item-pic {
    width: 100%;
    height: 17rem;
    cursor: pointer;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list-item-pic .lock{
    width:100%;
    height:100%;
    background: rgba(0, 0, 0, .35);
    border-radius: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    img{
        width:16%;
        display: block;
        height: auto;
    }
}

.list-item-pic img {
    display: block;
    width: 100%;
    height: auto;
    border-radius: 18px;
}

.list-item-title {
    padding: 10px 0 0 0;
    font-size: 16px;
    height: 40px;
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.list-item-people {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0 0 0;
    border-top:1px solid var(--deep-line);
}

.item-people {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.item-people .name {
    font-size: 16px;
    padding-left: 14px;
    color: var(--text-alt-color);
}

.time {
    font-size: 12px;
    color: var(--yellow);
    padding-bottom:10px;
}

.people-category {
    padding-top:10px;
}

.people-category span {
    display: inline-block;
    background-color: var(--light-white);
    padding: 3px 14px;
    font-size: 12px;
    border-radius: 18px;
    margin: 0 15px 0 0;
    color:var(--black);
}

.people-category span.short {
    background-color:var(--yellow);
}

.people-category span.long {
    background-color:var(--yellow);
}

.information {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
}

.information .name {
    font-size: 16px;
    .people-name{
        padding:0 10px 0 0;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        align-content: center;
        img{
            width:16px;
            height:auto;
            margin: 0 0 0 5px;
        }
    }
    .people-hot{
        margin-top: 4px;
        font-size: 12px;
        line-height: 17px;
        height: 17px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        align-content: center;
        img{
            width:16px;
            height:auto;
        }
    }
}

.people-buy{
    flex-shrink: 0;
    display: inline-block;
    color: var(--Or5);
    background-color: var(--Or1);
    border-radius: 4px;
    margin-top: 4px;
    font-size: 14px;
    padding: 4px 8px;
    cursor: pointer;
    margin-left:20px;
}

.people-notbuy{
    flex-shrink: 0;
    display: inline-block;
    color: var(--light-gray);
    background-color: var(--gray-line);
    border-radius: 4px;
    margin-top: 4px;
    font-size: 14px;
    padding: 4px 8px;
    cursor: pointer;
    margin-left:20px;
}


.information .time {
    font-size: 12px;
    margin: 5px 0 0 10px;
}

.item-other-comment {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-content: center;
}

.item-people img {
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.item-other-peo{
    margin-right: 0;
    border-radius: 30px 0 0 30px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-content: center;
}

.item-other-peo img {
    margin: 0;
    height: 34px;
    width: 34px;
    border-radius: 50%;
    margin-left: -10px;
}

.comment {
    text-align: center;
    padding-left: 16px;
    cursor: pointer;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-content: center;
}

.comment .numbers {
    color: var(--blue);
    padding-right: 5px;
}

.ant-pagination{
    margin:2rem 0 2rem 0;
}

//排行榜
.home-content-ranking {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    color: var(--white);
}

.home-ranking {
    width: 50%;
}

.ranking-title {
    font-size: 1.2rem;
    padding: 1rem 0;
}

.ranking-list {
    width: 80%;
}
.ranking-items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    padding-bottom: 20px;
}

.ranking-people {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
}

.ranking-num {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
}

.ranking-num {
    font-size: 1rem;
    padding-right: 1rem;
}

.ranking-num .num {
    padding-right: 1.5rem;
}

.ranking-people .information {
    font-size: 1rem;
    padding-left: 0.7rem;
}

//我的交易图表
.add-chart {
    padding: 1.2rem;
    margin: 1rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
}
.add-chart.add-empty {
    justify-content: center;
    flex-wrap:wrap;
}

.personal-img img {
    margin:0 auto;
    display: block;
    width: 100px;
    height: 100px;
    border-radius: 50%;
}


.add-chart.add-empty .personal-img{
    width:100%;
    margin-bottom:1rem;
}

.add-chart-name {
    background: var(--yellow);
    padding: 0.2rem 1rem;
    display: inline-block;
    font-size:1rem;
    box-shadow: 0 0 0 0.2rem var(--tab-color);
    border-radius: 1.8rem;
    color:var(--black);
}

.add-chart-name:hover {
    color: var(--black);
}

.chart-list {
    padding: 0 1rem 1.2rem 1rem;
}

.chart-list-items {
    border-radius: .8rem;
    background-color: var(--deep-black);
    padding: .8rem;
    margin-bottom: 1.2rem;
}

.chart-items-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    font-size: .8rem;
    border-bottom: 1px solid var(--border-color);
    padding: 0 0 .8rem 0;
    margin-bottom: 1.2rem;
    .chart-items-title{
        font-size:1rem;
    }
}

.chart-items-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    font-size: .7rem;
    border-top: 1px solid var(--border-color);
    padding: .8rem 0 0 0;
}

.chart-items-ratio {
    color: var(--text-alt-color);
}

.chart-list .chart-pic img {
    padding: 1rem 0;
    width: 12rem;
}

.chart-onepic {
    padding: 1.5rem 0 0 0;
}

.chart-items-operate {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-content: center;
}


@media (max-width: 767px){
  .list-nav-title{
        display: flex;
        justify-content:flex-start;
        align-items: center;
  }    
 .list-nav{
      padding: 1rem 2rem;
 }

 .list-nav-title-icon{
      position: relative;
      cursor:pointer;
      &:after{
          content: '';
          position: absolute;
          left: 0.5rem;
          top: 50%;
          height: 1.5rem;
          width: 1.5rem;
          background: url($imgBaseUrl + "/filter.png") no-repeat center center;
          background-size: cover;
          display: block;
          z-index: 15;
          -webkit-transform:translate(0px,-50%);
      }
  }

  .list-item {
      margin: 1.25rem 0 0 0;
      width:100%;
      min-width:100%;
      max-width:100%;
      background: var(--deep-black);
      padding: 0.5rem;
      border-radius: 0.8rem;
   }
   .list-item-people{
     padding:0;
     width:100%;
   }
    .item-people{
      width:100%;
    }
    .people-information{
      width:100%;
      .information{
          display: flex;
          justify-content:space-between;
          align-items: center;
          width:100%;
      }
  }
    .list-item-pic {
          width: 100%;
          height: 12rem;
          cursor: pointer;
          overflow: hidden;
          display: flex;
          justify-content: space-between;
          align-items: flex-start;
    }
    .item-other-comment {
      display: flex;
      justify-content: space-between;
      align-items: center;
      align-content: center;
      padding:1rem 0 0 0;
      .comment{
          padding:0 0.5rem;
      }
    }
    .item-other-peo{
      display:none;
    }
    .home-content-ranking{
        flex-wrap:wrap;
     }
    .home-ranking {
      width: 100%;
    }
    .ranking-list{
      width: 100%;
    }
    .chart-items-header{
      flex-wrap:wrap;
    }
    .chart-list{
      padding:0;
    }   
}